.badge {
  border-radius: 8px;
  padding: 0.5rem 1.2rem;
  display: inline-block;
  margin: .4rem;
}

.frontend {
  background-color: #3d008d;
  color: white;
}

.backend {
  background-color: #71008d;
  color: white;
}

.career {
  background-color: #8d006e;
  color: white;
}
</style>
